@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif; 
  font-size: 1.5rem;
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #f83920, #f3930d);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  /* align-items: center; */
}

.inputForm form {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  margin: 0 auto;
}
  
.inputForm input {
  border: none;
  border-bottom: 2px solid white;
  background-color: unset;
  outline: none;
  font-family: 'Poppins', sans-serif;
  font-size: 22px;
  color: white;
}

.inputForm input::-webkit-input-placeholder {
  color: white;
  font-size: 18px;
}

.inputForm input:-ms-input-placeholder {
  color: white;
  font-size: 18px;
}

.inputForm input::placeholder {
  color: white;
  font-size: 18px;
}
  
/* .inputForm label {
  position: absolute;
  left: 0%;
  color: white;
  transform-origin: 0 0;
  transition: all 0.3s;
} */
  
.inputForm .btn {
  border: none;
  background-color: unset;
  color: white;
  font-size: 22px;
  cursor: pointer;
  margin-left: 20px;
}
  
